@font-face {
  font-family: "CenturyGothic";
  src: url("../fonts/../fonts/CenturyGothic.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "CenturyGothic";
  src: url("../fonts/../fonts/CenturyGothic-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: bold;
}
@font-face {
  font-family: "Tetra";
  src: url("../fonts/../fonts/Tetra.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  color: var(--light-color);
  background-color: var(--dark-color);
  background-image: url("../img/background.jpg");
  background-repeat: no-repeat;
  background-position: center 100vh;
  background-size: cover;
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  line-height: 1.5;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

section {
  margin-top: 112px;
}
section:first-of-type {
  margin: 0;
}

.flex {
  display: flex;
}

.grid {
  display: grid;
  column-gap: var(--offset);
  grid-template-columns: repeat(12, 1fr);
}
@media (max-width: 768px) {
  .grid {
    column-gap: 16px;
    grid-template-columns: repeat(4, 1fr);
  }
}

h1,
h2,
h3,
h4 {
  margin: 0;
}

p {
  margin: 0;
}

.header {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  z-index: 11;
  padding: 10px 0;
}

.swiper__container-bg {
  height: 100%;
  position: relative;
  z-index: 0;
}
.swiper__container-title {
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: 10;
  top: 0;
  left: 0;
  cursor: default;
}
.swiper__title {
  font-family: var(--font-accent, sans-serif);
  letter-spacing: 20px;
  font-size: 200px;
  font-weight: bold;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  text-transform: uppercase;
  color: var(--white-color);
}
.swiper-pagination {
  right: 0;
  font-weight: bold;
}
.swiper-pagination-bullet {
  width: 40px;
  height: 40px;
  text-align: center;
  line-height: 40px;
  font-size: 16px;
  color: var(--light-color);
  opacity: 0.6;
  background: none;
  border: none;
}
.swiper-pagination-bullet-active {
  opacity: 1 !important;
  transform: scale(1.4);
}

.hero__inner {
  position: relative;
  height: 100vh;
  overflow-x: hidden;
}
.hero__slide-one {
  background-color: #d7aa06;
  background-image: url("../img/slide1.jpg");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}
.hero__slide-two {
  background-color: #4e8626;
  background-image: url("../img/slide2.jpg");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}
.hero__slide-three {
  background-color: #dcae8c;
  background-image: url("../img/slide3.jpg");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}
.hero__container {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 0;
  width: 100%;
  height: 100%;
}
.hero__human {
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  z-index: 1;
}

.fonts__inner {
  justify-content: space-between;
  gap: 40px;
}
.fonts__block {
  max-width: 350px;
  text-align: center;
}
.fonts__block-accent {
  font-family: var(--font-accent);
  letter-spacing: 1px;
}
.fonts__block-accent .fonts__title::after {
  content: "03";
}
.fonts__block-text .fonts__title::after {
  content: "01";
}
.fonts__block-bold {
  font-weight: bold;
}
.fonts__block-bold .fonts__title::after {
  content: "02";
}
.fonts__title {
  font-family: inherit;
  font-weight: inherit;
  font-size: 22px;
  margin-bottom: 40px;
  position: relative;
  text-align: center;
}
.fonts__title::after {
  content: "01";
  position: absolute;
  left: 15%;
  top: 50%;
  transform: translateY(-50%);
  width: 10px;
  height: 10px;
  font-size: 60px;
  font-family: var(--font-family);
  font-weight: bold;
  line-height: 10px;
  opacity: 0.4;
  background-image: url("./../");
  background-repeat: no-repeat;
}
.fonts__text {
  overflow-wrap: break-word;
  word-wrap: break-word;
  -ms-word-break: break-all;
  word-break: break-word;
}

.colors__inner {
  justify-content: space-around;
  gap: 40px;
}
.colors__block {
  text-align: center;
  flex-direction: column;
  align-items: center;
}
.colors__image {
  margin-bottom: 10px;
}
.video__inner {
  position: relative;
}
.video__tablet {
  max-width: 1170px;
  width: 100%;
  max-height: 685px;
  object-fit: fill;
}
.video__content {
  position: absolute;
  left: 8px;
  top: 30px;
  max-width: 1154px;
  border-radius: 40px;
}

.about__inner {
  align-items: center;
  justify-content: center;
}
.about__logo {
  width: 100%;
  grid-column: 3/8 span;
  border-radius: 30px;
}
.about__description {
  grid-column: 2/10 span;
  height: min-content;
  text-align: justify;
}

.gallery__inner {
  margin: 0 auto;
  max-width: 1920px;
  padding: 0 15px;
  row-gap: 50px;
  align-items: center;
  justify-content: center;
}
.gallery__full {
  height: 1080px;
  background-color: #eeeded;
  background-image: url("../img/pattern-full.png");
  background-position: center center;
  background-size: 960px;
  background-repeat: repeat;
  border-radius: 30px;
  grid-column: 12 span;
}
.gallery__pattern {
  width: 100%;
  height: 100%;
  max-height: 500px;
  background-color: #eeeded;
  padding: 10px;
  border-radius: 30px;
}
.gallery__pattern-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.gallery__pattern-one {
  grid-column: 4 span;
}
.gallery__pattern-two {
  grid-column: 5/4 span;
}
.gallery__pattern-three {
  grid-column: 9/4 span;
}
.gallery__pattern-four {
  grid-column: 4 span;
}
.gallery__pattern-five {
  grid-column: 5/4 span;
}
.gallery__pattern-six {
  grid-column: 9/4 span;
}